@import "./variables.scss";
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,500;0,700;1,400&display=swap");

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
  font-family: "Roboto", sans-serif;
  color: $color-grey-1;
  font-weight: 400;
  background-color: $color-grey-light;
}

h1,
h2,
h3 {
  margin-bottom: 0.6rem;
}

p {
  margin-bottom: 4px;
}

.row {
  max-width: 90%;
  margin: 0 auto;
}

.header {
  background-color: #fff;
  border-bottom: 1px solid $color-border;

  .logo {
    &:link,
    &:visited {
      text-decoration: none;
      color: $color-blue;
    }
  }

  .header-container {
    display: flex;
    align-items: center;
    height: 6rem;
    gap: 4rem;
  }

  .user {
    margin-left: auto;

    .icon {
      box-sizing: content-box;
      padding: 1rem;
      color: $color-blue;
      font-size: 1.8rem;
      border: solid 1px $color-border;
      border-radius: 50%;
    }
  }
}

.nav {
  height: 100%;

  .nav-links {
    height: 100%;
    list-style: none;
    display: flex;
    align-items: center;
    gap: 1rem;

    .nav-link {
      a {
        &:link,
        &:visited {
          text-decoration: none;
          color: $color-grey-2;
          transition: all 0.2s;
        }

        &:hover,
        &:active {
          color: $color-grey-1;
        }

        &.active-link {
          color: $color-grey-1;
        }
      }
    }
  }
}

.books-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding-top: 2rem;
  padding-bottom: 4rem;
}

.book-container {
  background-color: #fff;
  border: 1px solid $color-border;
  padding: 2rem;
  display: flex;
  align-items: center;
  gap: 2rem;

  & > * {
    flex: 1;
  }

  .book-details {
    .title {
      font-size: 1.8rem;
    }

    .by {
      font-size: 1.5rem;
    }

    .buttons-wrapper {
      display: flex;
      align-items: center;
      gap: 2rem;
      margin-top: 2rem;

      button {
        border: none;
        background-color: transparent;
        cursor: pointer;
        font-size: 1.5rem;
        font-family: inherit;
        color: $color-blue-dark;
      }
    }
  }

  .completion-status {
    display: flex;
    gap: 2rem;

    .completion-data {
      display: flex;
      flex-direction: column;

      .percentage {
        font-size: 3rem;
        font-weight: 300;
      }

      .status {
        font-size: 1.3rem;
        color: $color-grey-2;
      }
    }
  }

  .chapter {
    .title {
      color: $color-grey-2;
      font-size: 1.3rem;
      text-transform: uppercase;
      margin-bottom: 0.5rem;
    }

    .chapter-text {
      font-size: 1.6rem;
    }

    .progress-btn {
      padding: 0.6rem 1.2rem;
      border-radius: 3px;
      background-color: $color-blue;
      color: #dbdbdb;
      cursor: pointer;
      border: none;
      margin-top: 1rem;
    }
  }
}

.form-wrapper {
  border-top: solid 2px $color-border;
  padding: 4rem 0;

  .form {
    display: flex;
    gap: 2rem;

    .title-input,
    .author-input {
      padding: 0.8rem 1.2rem;
      border: 1px solid $color-border;
      border-radius: 3px;
      background-color: #fff;
    }

    .title-input {
      flex: 3;
    }

    .author-input {
      flex: 2;
    }

    .submit-button {
      flex: 1;
      cursor: pointer;
      border: none;
      color: #fff;
      font-size: 1.4rem;
      background-color: $color-blue;
      border-radius: 3px;
    }
  }
}

@media (max-width: 600px) {
  .book-container {
    .completion-status {
      display: none;
    }
  }

  .form-wrapper {
    .form {
      flex-direction: column;
      gap: 1rem;

      .submit-button {
        padding: 0.6rem 1.2rem;
      }
    }
  }
}
